import axios from "axios";
import secureStorage from "@/utils/secureStorage";

// Define a URL base corretamente
const baseURL = `${process.env.VUE_APP_API_URL || "http://localhost:8000"}/api`;

// Log de depurao opcional
console.log(" API Base URL:", baseURL);

const api = axios.create({
  baseURL,
  withCredentials: true, 
});

// Interceptor para adicionar o token automaticamente
api.interceptors.request.use((config) => {
  const token = secureStorage.getItem("token");
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Interceptor 
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response) {
      console.error(
        " Erro na resposta da API:",
        error.response.status,
        error.response.data
      );
    } else if (error.request) {
      console.error(" Nenhuma resposta do servidor:", error.request);
    } else {
      console.error("Erro ao configurar a requisio:", error.message);
    }
    return Promise.reject(error);
  }
);

export default api;
